@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600&display=swap);
body, html {
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
    scroll-behavior: smooth;
    -webkit-overflow-scrolling: touch;
    font-family: 'Open Sans', sans-serif;
}

* {
    transition: all .3s;
}

h1, p {
    font-family: 'Open Sans', sans-serif;
}

.content {
    width: 90%;
    margin: 0px auto;
    max-width: 1000px;
}
.container-slide {
    position: relative;
    min-height: 100vh;
}

.container-content {
    width: 100%;
    height: -webkit-max-content;
    height: max-content;
    margin: 0;
    top: 50%;
    left: 50%;
    display: flex;
}

.paper {
    height: 315px;
    padding: 15px;
    text-align: center;
    position: relative;
}

.paper div {
    padding: 0 15px;
    width: 100%;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

.paper div a {
    margin: 10px;
}

/* Responsive */
@media screen and (max-width: 600px) {
    .container-content {
        display: block;
        margin-top: 60px;
        height: -webkit-max-content;
        height: max-content;
    }

    .container-content .wrapper {
        width: 100%;
        height: 350px;
    }
}

@media screen and (max-height: 630px) and (min-width: 600px) {
    .container-content {
        width: 100%;
        height: 315px;
        margin: 0;
        display: inherit;
        height: -webkit-max-content;
        height: max-content;
        margin-top: 60px;
    }
}
.header-container {
    display: flex;
    position: fixed;
    width: 100%;
    z-index: 100;
    background-color: #2c302c;
}

.header-container div {
    justify-content: space-between;
    color: #defff2;
}

.header-buttons span {
    color: #defff2;
}

#logo {
    width: 50px;
    height: 50px;
}

.logo-text span {
    font-family: 'Segoe UI';
    text-transform: none;
    font-size: 16px;
    text-align: center;
    color: #defff2;
}

.logo-text b {
    color: #b56eff;
}
.container {
    width: 100%;
    height: 100vh;
    background-color: #D9E4FB;
}

h1 {
    margin: 0;
    padding: 0;
}

.typography {
    position: relative;
    height: -webkit-max-content;
    height: max-content;
    min-height: 100vh;
    display: flex;
    align-items: center;
}

@media screen and (max-width: 600px) {
    .typography {
        height: 100%;
    }
}
@media screen and (max-width: 600px) {
    .wrapper {
        width: 100%;
    }
}

.small {
    width: 30%;
    min-width: 280px;
    margin: auto;
}

.large {
    width: 70%;
}
#about {
    background-image: url(/business-react-landing-page/static/media/waveTop.93aca2e1.svg);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    margin-bottom: -1px;
}

@media screen and (max-width: 600px) {
    #about {
        min-height: 800px;
    }
}
@media screen and (max-width: 704px) and (min-width: 600px) {
    #projects {
        padding-top: 50px;
    }
}

#projects-ref {
    position: absolute;
    top: -70px;
}
code {
    font-size: 14px;
    background-color: lightgray;
    border-radius: 5px;
    padding: 2px;
}

.Individual {
    background-color: lightgreen;
}

.Group {
    background-color: orange;
}

.Pairs {
    background-color: #04d9ff;
}

h1 {
    margin-bottom: 5px;
}

.container-slide {
    background-image: url(/business-react-landing-page/static/media/waveMid.2b84f1f6.svg);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}

#coding-challenges-ref1 {
    top: -55px;
    position: absolute;
}

#coding-challenges-ref2 {
    top: -45px;
    position: absolute;
}

#contact {
    background-image: url(/business-react-landing-page/static/media/waveBott.d3be9933.svg);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    margin-bottom: -1px;
}

.paper-form {
    max-width: 400px;
    width: 90%;
    text-align: center;
    margin: auto;
    position: relative;
}

.paper-form .material-input {
    margin: 10px auto;
    display: block;
    width: 100%;
}

.MuiInputBase-root {
    width: inherit;
}

form {
    margin: 10px;
}
.socialMedia-box {
    width: 100%;
    min-height: 50px;
    background-color: #4e47ef;
    padding: 5px;
    display: flex;
    align-items: center;
    justify-content: space-around;
    position: relative;
}

.icon {
    width: 100%;
    height: 25px;
}

.box:hover {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    cursor: pointer;
}

.box {
    margin: 5px;
    width: -webkit-max-content;
    width: max-content;
    height: -webkit-max-content;
    height: max-content;
    min-height: 27px;
    min-width: 27px;
}
.footer-container {
    width: 100%;
    min-height: 50px;
    background-color: #2a4d69;
    display: flex;
    align-items: center;
}

.content-box {
    width: 90%;
    margin: 0 auto;
    color: #fff;
    padding: 5px;
}
